import clsx from 'clsx';
import { Slot } from '@radix-ui/react-slot';
import { ReactNode } from 'react';

export interface ButtonProps {
  children: ReactNode;
  asChild?: boolean;
}

export function Button({ children, asChild }: ButtonProps) {
  const Comp = asChild ? Slot : 'button';

  // Slot pega todas as classes aplicadas nele e passa pro primeiro elemento que vem no objeto children

  return (
    <Comp
      className={
        clsx("py-3 px-4 bg-cyan-500 rounded hover:bg-cyan-300 text-black text-sm font-semibold w-full transition-colors focus:ring-2 ring-white")
      }
    >
      {children}
    </Comp>
  );
}